/* BOX LOGIN HEADER (ridefinizione colori)
================================================== */
.hoverDropDown{
  &:hover {
    .dropdDownBox {
      display: block;
    }
  }
  position: relative;
  .trigger{
    position: relative;
    z-index: 99;
  }
  .dropdDownBox {
    .box-shadow(0 10px 10px rgba(0,0,0,0.2));
    display:none;
    padding: @baseDim / 2;
    position:absolute;
    top: 15px;
    width: 200px;
    z-index: 9999;
    background: @white;
    right: 10px;
    &.open{
      display: block;
    }
    ul {
      li {
        text-align: left;
        padding: @baseDim / 4;
        display: block !important;
        font-size: @font-size - 4;
        a{
          color: @beige !important;
          .sole_text();
          &.active{
            color: @black !important;
            font-weight: @bold;
            &:after{
              font-family: 'icomoon';
              content: "\e623";
              float: right;
            }
          }
        }
      }
    }
  }
}

@import "tendina-social.less";

#topw {
  background: @white;
  .container {
    padding: @baseDim / 2 0;
    position: relative;
    .montserrat();
    strong {
      font-weight: @bold;
    }
    nav {
      font-size: 0;
      line-height: 1em;
      li {
        display: inline-block;
      }
      a {
        color: @primary;
        font-size: @font-size - 4;
        line-height: 1em;
        .icon-star-empty {
          color: @black !important;
        }
      }
      &.text-left {
        a {
          font-size: @font-size - 3;
          padding-right: @baseDim / 2;
          margin-right: @baseDim / 2;
          &.last {
            border-right: none;
            padding-right: 0;
            margin-right: 0;
          }
        }
      }
      &.text-right {
        > li > a {
          padding-right: @baseDim / 2;
          border-right: 1px solid @beige;
          margin-right: @baseDim / 2;
          &:hover {
            text-decoration: none;
          }
          &.last {
            border-right: none;
            padding-right: 0;
            margin-right: 0;
          }
          &.custom {
          }
          span {
            margin-left: 4px;
            &.icon-my24, &.icon-star, &.icon-star-empty {
              color: @primary !important;
            }
          }
        }
      }
    }
  }
}

header#header {
  padding-top: @baseDim;
  
  .identita {
    // padding-bottom: @baseDim / 2;
  }
  
  .pos {
    margin-top: -@baseDim / 4;
    @media (max-width: 979px) {
      margin-top: @baseDim / 4;      
    }
  }
  
  .thumbnail {
    margin: 0;
    img {
      margin: 0;
    }
  }
  
  .bordered {
    vertical-align: top;
    display: inline-block;
    width: 150px;
    border-left: 1px solid @beige;
    padding-left: @baseDim / 2;
    margin-left: @baseDim;
    margin-top: @baseDim;
    position: relative;
    padding-bottom: @baseDim;
    p {
      font-size: @font-size - 5;
      line-height: 1em;
      font-style: italic;
      color: @beige_d;
    }
    .by {
      position: absolute;
      top: 10px;
      left: 10px;
      font-style: italic;
      color: @brown;
      font-size: @font-size - 5;
    }
  }
  
  .sticky_header {
    z-index: 5;
    background: @salmone;
  }
  
  .sezioni {
    background: @beige;
    height: @baseDim * 2;
    margin-top: @baseDim / 2;
    ul {
      font-size: 0;
      li {
        display: inline-block;
        vertical-align: top;
        .btn-menu {
          display: block;
          padding: @baseDim / 2;
          height: 20px;
          line-height: 20px;
          font-size: @font-size - 2;
          color: @white;
          &:hover {
            text-decoration: none;
            color: @primary_d;
          }
          small {
            font-size: @font-size - 4;
          }
          &.active {
            background: @primary_d;
            padding-left: @baseDim;
            padding-right: @baseDim;
            &:hover {
              color: @white;
            }
          }
        }
        [data-scadenze]:after {
          display: block;
          margin: -40px 0 0 65px;
          padding: 4px;
          float: right;
          position: absolute;
          background: @red;
          color: @white;
          font-size: 10px;
          .montserrat();
          line-height:1em;
          text-align:center;
          border: 1px solid @white;
          border-radius: 100px;
          content: attr(data-scadenze);
        }
      }
      &#nav-tools {
        text-align: right;
        li {
          position: relative;
          padding: 0 @baseDim / 4;
          &:after {
            content: '';
            display: block;
            height: @baseDim;
            width: 1px;
            background: @beige_d;
            position: absolute;
            top: 50%;
            margin-top: -@baseDim / 2;
            right: 0;
          }
          &.last {
            padding-right: 0;
            &:after {
              display: none;
            }
          }
          a.btn-tools {
            display: block;
            text-align: center;
            padding: @baseDim / 2;
            font-size: @font-size;
            line-height: @baseDim;
            height: @baseDim;
            span {
              color: @white !important;
            }
            &:hover, &.active {
              text-decoration: none;
              span {
                color: @primary !important;
              }
            }
          }
        }
      }
    }
  }
  
  .filtri {
    padding: @baseDim / 2 0 @baseDim;
    display: block;
    background: @primary_d;
    &.open {
      display: block !important;
    }
    h6 {
      position: relative;
      color: @primary_l;
      font-size: @font-size - 8;
      line-height: @font-size - 2;
      padding-left: @baseDim / 2;
      margin-bottom: @baseDim / 4;
    }
    ul {
      font-size: 0;
      li {
        display: inline-block;
        overflow: hidden;
        vertical-align: top;
        padding-left: @baseDim / 2;
        padding-right: @baseDim / 2;
        border-left: 1px solid @primary_l;
        &.first {
          margin-left: 0;
          border: none;
        }
        &.last {
          a {
            padding-right: 0;
          }
        }
        a {
          display: block;
          overflow: hidden;
          position: relative;
          padding-left: @baseDim / 4 * 3;
          font-size: @font-size - 6;
          line-height: 1em;
          .montserrat_bold();
          text-transform: uppercase;
          color: @primary;
          height: 24px;
          span {
            position: absolute;
            top: 0;
            left: 0;
            display: none;
            width: @baseDim / 2;
            height: 100%;
          }
          &.active {
            text-decoration: none;
            color: @white;
            span {
              display: block;
            }
          }
          &.noco {
            padding-left: 0;
          }
        }
      }
    }
  }
  
  .is_stuck {
    .filtri {
      display: none;
    }
  }

}


#search {
  z-index: 50;
  position: absolute;
  top: -10px;
  padding: 10px 0 0;
  background: @salmone;
  right: 74px;
  left: 103px;
  visibility: hidden;
  .opacity(0);
  &.open {
    visibility: visible;
    .opacity(100);
  }
  #tool-ricerca {
    border: 4px solid @beige;
    background: @white;
    width: 100%;
    height: 32px;
    position: relative;
    margin:0;
    font-size: 0;
    li {
      vertical-align: top;
      display: inline-block;
      float: none !important;
      margin: 0;
      &.li-input {
        height: 32px;
        overflow: hidden;
      }
      &.li-submit {
        position: absolute;
        top: 50%;
        right: 0;
        margin-top: -16px;
      }
      &.li-tag {
        font-size: @font-size - 6;
        padding: 4px 4px 0;
        .montserrat();
        height: 28px;
        overflow: hidden;
        span {
          &.tag-wrapper {
            display: block;
            border: 1px solid #ddd;
            .border-radius(4px);
            padding: 1px 6px;
            background: @hover;
            color: @gray;
            height: 20px;
            line-height: 20px;
          }
        }
        a.tag-close {
          padding-left: 5px;
          background: none;
          cursor: pointer;
          span {
            color: @gray;
            line-height: 20px;
            font-size: 12px;
          }
        }
      }
      .dropdown-menu {
        font-size: @font-size - 6;
        .border-radius(0);
        margin-top: 0;
        padding: 0;
        border-color: @hover;
        .montserrat();
        line-height: 1em;
        li {
          display: block;
          &.active, &:hover {
            background-image: none;
            background: @hover;
          }
          a {
            padding: 7px @baseDim;
            background: none;
            color: @black;
            strong {
              font-weight: @bold;
            }
          }
        }
      }
    }
  }
  .ricerca-keyword {
    margin:0;
    padding:9px 5px 6px;
    // float:left;
    width: 150px;
    font-size:14px;
    // .montserrat();
    line-height:1.2;
    color:@black;
    border:none;
    outline:none;
    -webkit-appearance:none;
  }
}
.ricerca-submit, .btn-tm {
  display: block;
  width: 32px;
  height: 32px;
  line-height: 32px;
  padding: 0;
  background: @beige_d;
  color: @white;
  cursor: pointer;
  font-size: @font-size;
  border: none;
  outline: none;
  text-align: center;
}
.ricerca-submit {
  float:left;
  .icon-chevron-right {
    display: none;
  }
}
.relative {
  position: relative;
}
.btn-tm {
  position: absolute;
  top: 4px;
  right: 0;
  width: 18px;
  height: 18px;
  line-height: 18px;
  padding: 7px;
  &:hover {
    text-decoration: none;
    color: @white;
  }
}
.relative {
  position: relative;
}
.btn-tm {
  position: absolute;
  top: 4px;
  right: 0;
  width: 18px;
  height: 18px;
  line-height: 18px;
  padding: 7px;
  &:hover {
    text-decoration: none;
    color: @white;
  }
}


/* //////////////////////////// */
/* Testata per tablet verticale */
/* //////////////////////////// */
@media (max-width: 979px) {
  #cerca {
    width: 178px;
    #tool-ricerca {
      width: 178px;
    }
  }
  header#header {
    .identita {
      img {
        // width: 60%;
      }
    }
    .filtri {
      padding-bottom: @baseDim / 4 * 3;
      ul {
        li {
          padding-left: @baseDim / 4;
          padding-right: @baseDim / 4;
          a {
            padding-left: @baseDim / 2 + 5;
            font-size: @font-size - 8;
            line-height: 1em;
            height: 20px;
            span {
              width: @baseDim / 4 + 3;
            }
          }
        }
      }
    }
  }
}




/* /////////////////////////// */
/* Testata per tablet e mobile */
/* /////////////////////////// */

#mobile_menu, #sticky_header_phone{
  display: none;
}

@menuWidth : 290px;
@stickHeaderPhoneHeight : 40px;


@media (max-width: 767px) { // era 979px

  .sticky_header {
    display: none;
  }
  #sticky_header_phone {
    height: @stickHeaderPhoneHeight;
    display: block;
    z-index: 30;
    background: @primary;
    .container {
      position: relative;
      menu {
        height: @stickHeaderPhoneHeight;
        a {
          padding-top: @baseDim / 4;
          height: @stickHeaderPhoneHeight - (@baseDim / 4);
          width: @stickHeaderPhoneHeight;
          text-align: center;
          display: inline-block;
          margin-right: @baseDim / 2;
          color: @white;
          span {
            font-size: @font-size + 10;
          }
          &.nav_show {
            .icon-remove {
              display: none;
            }
          }
          &.search, &.my24-log {
            display: none;
          }
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    #cerca {
      position: absolute;
      top: 0;
      right: @baseDim;
    }
  }

  
  html, body{
    height: 100%;
  }
  #pg_wrapper {
    overflow: visible;
    position: relative;
    height: auto;
    width: 100%;
  }
  #wrapper {
    position: relative;
    height: 100%;
    min-height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    //z-index: 1;
  }

  #mobile_menu {
    position: relative !important;
    width: @menuWidth !important;
    z-index:0;
    min-height: 100%;
    height: 100%;
    display: none;
    float: left;
    background: @primary;
    overflow: hidden;
    color: @white;
    
    .filtri {
      position: absolute;
      z-index: 20;
      left: 0;
    }
    
    .container {
      width: @menuWidth - (@baseDim * 2);
      padding: 0 @baseDim !important;
    }
    .row {
      margin: 0;
    }
    [class*="span"] {
      width: 100%;
      float: none;
      margin: 0;
    }
    a {
      color: @white;
      &:hover {
        text-decoration: none;
      }
    }
    .sezioni {
      padding-bottom: @baseDim;
      ul {
        li {
          &.pref {
            .btn-menu.active {
              background: @primary_d !important;
            }
          }
          .btn-menu {
            display: block;
            margin: 0 -@baseDim;
            padding: @baseDim / 2 @baseDim;
            height: 26px;
            line-height: 26px;
            font-size: @font-size + 2;
            small {
              font-size: @font-size + 2;
            }
            span {
              color: transparent;
              font-size: @font-size - 6;
              &.icon-star-empty, &.icon-star, &.icon-time-machine {
                font-size: @font-size - 2;
                color: @white !important;
              }
            }
            &.active {
              background: @primary_d;
              span {
                color: @white;
              }
            }
          }
          [data-scadenze]:after {
            display: block;
            margin: -24px 0 0 100px;
            padding: 4px;
            float: right;
            position: absolute;
            background: @red;
            color: @white;
            font-size: 10px;
            .montserrat();
            line-height: 1em;
            text-align:center;
            border: 1px solid @white;
            border-radius: 100px;
            content: attr(data-scadenze);
          }
        }
      }
    }
    
    .filtri {
      display: block;
      background: @primary_d;
      &.open {
        display: block !important;
      }
      br {
        display: none;
      }
      h6 {
        position: relative;
        color: @primary_l;
        font-size: @font-size - 8;
        line-height: 1em;
        margin-bottom: @baseDim / 2;
        &:before {
          content: "";
          display: block;
          height: 1px;
          width: 200px;
          background: @primary_l;
          position: absolute;
          top: 50%;
          margin-top: -1px;
          left: auto;
          right: 0;
        }
      }
      ul, ol {
        li {
          &.first {
            margin-left: 0;
            border: none;
          }
          &.last {
            a {
              padding-right: 0;
            }
          }
          a {
            display: block;
            overflow: hidden;
            padding-right: @baseDim / 4 * 3;
            position: relative;
            font-size: @font-size - 8;
            line-height: @font-size - 2;
            .montserrat_bold();
            text-transform: uppercase;
            span {
              position: absolute;
              top: 50%;
              margin-top: -@baseDim / 4;
              bottom: auto;
              right: 0;
              display: none;
              width: @baseDim / 2;
              height: @baseDim / 2;
            }
            &.active {
              text-decoration: none;
              color: @white;
              span {
                display: block;
              }
            }
          }
        }
      }
      ul {
        li {
          display: block;
          overflow: hidden;
          a {
            padding: @baseDim / 4 0;
            height: auto;
            margin-top: 0;
            margin-bottom: @baseDim / 4;
            color: @primary;
            font-weight: @regular;
            font-size: @font-size - 6;
          }
        }
        &.filtri-scad {
          a {
            height: 12px;
            padding-right: @baseDim;
          }
        }
      }
      ol {
        padding-top: 1px;
        li {
          a {
            padding: 1px 0;
            margin-bottom: 1px;
            color: @primary;
          }
        }
      }
      .time-machine {
        position: relative;
        padding: @baseDim / 2 0 @baseDim / 2 40px;
        margin: 0 -@baseDim;
        background: @primary_dd;
        height: auto;
        .icon-time-machine {
          color: @white;
          font-size: 18px;
          position: absolute;
          top: 50%;
          left: @baseDim;
          margin-top: -9px;
        }
        ol {
          font-size: 0;
          height: 16px;
          padding: @baseDim / 2 0;
          margin: 0;
          li {
            height: 16px;
            line-height: 16px;
            display: inline-block;
            padding: 0 6px;
            margin: 0;
            border-right: 1px solid @primary;
            &:last-child {
              border: none;
            }
            a {
              font-size: @font-size - 6;
              font-weight: @regular;
              padding: 0;
              margin: 0;
            }
          }
        }
      }
    }
    
  }
  

  html.menu_open {
    #pg_wrapper{
      overflow: hidden;
      min-height: 100%;
      height: auto !important;
      background: @primary;
    }
    #mobile_menu {
      z-index:20;
      display: block;
      min-height: 100%;
    }
    #wrapper {
      left: (@menuWidth);
      position: absolute;
      overflow: hidden;
      background: @salmone;
      top: @stickHeaderPhoneHeight;
    }
    #sticky_header_phone {
      .container {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        #cerca {
          display: none;
        }
        menu {
          .search, .my24-log {
            display:none;
          }
          a {
            span {
              &.icon-remove {
                display: block;
              }
              &.icon-reorder {
                display: none;
              }
            }
          }
        }
      }
      .nav_show {
        margin-left: 20px;
      }
    }
    
    #sidenav {
      display: none;
    }
  }

}


@media (max-width: 767px) {
  #topw {
    background: @primary;
    height: 0;
    .container {
      padding: 0;
    }
  }
  
  header#header {
    .identita {
      img {
        width: 260px;
      }
    }
  }
  
  #sticky_header_phone {
    .container {
      menu {
        a {
          &.search {
            display: inline-block;
          }
          &.user-log {
            display: inline-block;
            float: right;
            width: 100px;
            margin: 0;
          }
          &.user-log {
            width: 60px;
          }
          &.active {
            background: @primary_d;
          }
        }
      }
      #search {
        display: none;
        position: relative;
        top: auto;
        right: auto;
        left: 0;
        width: auto;
        margin: 0 -@baseDim;
        padding: 0 @baseDim;
        background: @white;
        z-index: 20;
        // border-top: 2px solid @carta;
        // border-bottom: @baseDim / 2 solid @salmone;
        .content {
          padding: 0;
          &:after {
            display: none;
          }
        }
        &.open {
          display: block;
        }
        #tool-ricerca {
          border: none;
          float: none;
          width: 100%;
          padding: @baseDim / 4 0;
        }
        .ricerca-submit {
          background: none;
          span {
            color: @primary;
            &.icon-search {
              display: none;
            }
            &.icon-chevron-right {
              display: block;
            }
          }
        }
      }
    }
    &.is_stuck {
      #cerca {
        border-bottom: none;
      }
    }
  }
  
  #headerLogin {
    height: 0;
    .accedi {
      display: none;
    }
    .headerLogin-box {
      display: none;
      position: fixed;
      top: @stickHeaderPhoneHeight;
      right: @baseDim;
      &.open {
        display: block !important;
      }
    }
  }
  
  .hoverDropDown{
    height: 0;
    .dropdDownBox{
      display: none;
      position: fixed;
      top: @stickHeaderPhoneHeight;
      right: @baseDim;
      &.open {
        display: block !important;
      }
    }
  }
}




/* //////////////////////////// */
/* No touch device hover status */
/* //////////////////////////// */
.notouch {
  header#header {
    .sezioni {
      ul {
        li {
          &.pref {
            .btn-menu {
              &:hover {
                span {
                  &.icon-star-empty, &.icon-star {
                    color: @white !important;
                  }
                }
              }
            }
          }
          .btn-menu {
            &:hover {
              span {
                &.icon-star-empty, &.icon-star {
                  color: @primary_d !important;
                }
              }
            }
          }
        }
      }
    }
    .filtri {
      ul, ol {
        li {
          a {
            &:hover {
              text-decoration: none;
              color: @white;
              span {
                display: block;
              }
            }
          }
        }
      }
    }
  }
}




