.scadhead {
  margin:@baseDim 0 @baseDim;
  .btn-qdf {
    width:90px;
  }
  .navimonth {
    padding-top:2px;
    float:left;
    li {
        float:left;
        margin-right:@baseDim/2;
        a {
            display:block;
            padding:8px 10px 6px;
            color:@primary;
            background:@border;
            line-height:1;
            text-decoration:none;
        }
    }
  }
  h2 {
    .sole_headline_bold();
    color:@primary;
    line-height: @font-size * 2;
    float:left;
    margin: 0 0 0 @baseDim;
  }
  .tipo-vista {
    float:right;
    li {
      float:left;
      display:inline-block;
      margin-left:@baseDim / 2;
    }
  }
}
.area_filters {
  margin-bottom:@baseDim * 2;
  li{
    float:left;
    display:block;
    margin-right:@baseDim / 2;
    span {
      margin-right:@baseDim / 2;  
    }
    .btn-qdf {
      border-top:3px solid;
      background:none;
      color:@black;
      &.active {
        color:@white;    
      }
      &.enti_PA{
        border-top-color: @tas09;
      }
      &.fisco{
        border-top-color: @tas10;
      }
      &.lavoro{
        border-top-color: @tas11;
      }
    }
  }
}

.btn-qdf {    
  &.active {
    color:@white;
    border:none;
    &.enti_PA{
      background-color: @tas09;
    }
    &.fisco{
      background-color: @tas10;
    }
    &.lavoro{
      background-color: @tas11;
    }
  }
}

#calendar {
  border-top:1px solid @border;
  table {
    width:100%;
    margin-bottom:@baseDim * 2;
    line-height:1;
    text-align:center;
    border-bottom:@baseDim / 2 solid @border;
    tr:first-child{
      color:@primary_d;
      .montserrat_bold();
      font-size:@font-size - 6;
      text-transform:uppercase;
      td {
        height:auto;
        padding:(@baseDim - 2) 0 (@baseDim / 2);
        background:@salmone;
      }
    }
    tr {
      color:@white;
      .montserrat_bold();
      font-size:@font-size - 6;
      td {
        height: 157px;
        width:14%;
        background:@calendario;
        vertical-align:top;
        border-right:1px solid @salmone;
        &.with-events {
          // background:@calendario_a;
          .item {
            display:none;
            min-height:24px;
            padding:(@baseDim - 12) (@baseDim / 4);
            font-size:@font-size;
            text-align:left;
            cursor:pointer;
            &.active {
              display:block;
            }
            &.enti_PA{
              background-color: @tas09;
            }
            &.fisco{
              background-color: @tas10;
            }
            &.lavoro{
              background-color: @tas11;
            }
            &:last-child {
              margin-bottom:1px;
            }
            strong {
              // width:90px;
              display:block;
              margin:-18px 0 0 30px;
              font-size:@font-size - 6;
              font-weight:700;
            }
          }
        }
        &.filled {
          background:@calendario_a;
        }
        &.disabled {
          background-color:@salmone;
          .daynum {
            color:@salmone;
          }
        }
        &.today {
          .daynum{
            border-top:5px solid @primary;
            padding-top:(@baseDim / 2) - 5;
          }
        }
        .daynum {
          display:block;
          padding:(@baseDim / 2) 0;
          background:@border;
          color:@primary;
          .sole_headline_bold();
          font-size:@font-size - 2;
        }
      }
    }
  }
  // &.cal-week {
  //   tr {
  //     td {
  //       &.with-events {
  //         .item {
  //           padding:@baseDim @baseDim / 4;
  //           strong {
  //             margin:(@baseDim / 2) 0 0;
  //             height:@baseDim + 2;
  //           }
  //         }
  //       }
  //     }
  //   }
  // }
  &.cal-day {
    padding-top:@baseDim;
    .scadenze-giorno {
      li {
        border-bottom:1px solid @border;
        padding-bottom:@baseDim;
        margin-bottom:@baseDim + 10;
        h3, p {
          margin:0;
        }
      }
    }
  }
}

@media (max-width: 979px) {
  #calendar table tr td.with-events .item strong {
    font-size: @font-size - 10;
  }
}