/* colori tassonomia */
@tas01: #ffa800;
@tas02: #e35c09;
@tas03: #cd0a07;
@tas04: #bd2666;
@tas05: #6a7b85;
@tas06: #379193;
@tas07: #74875d;
@tas08: #624403;

/* colori tassonomia scadenze */
@tas09: #ce8515;
@tas10: #72b7e6;
@tas11: #ed8176;

/* COLORI
================================================== */
.tas01, h6.tas01 a { color: @tas01; &:hover { color: @tas01; } } .tas01-b { background: @tas01; }
.tas02, h6.tas02 a { color: @tas02; &:hover { color: @tas02; } } .tas02-b { background: @tas02; }
.tas03, h6.tas03 a { color: @tas03; &:hover { color: @tas03; } } .tas03-b { background: @tas03; }
.tas04, h6.tas04 a { color: @tas04; &:hover { color: @tas04; } } .tas04-b { background: @tas04; }
.tas05, h6.tas05 a { color: @tas05; &:hover { color: @tas05; } } .tas05-b { background: @tas05; }
.tas06, h6.tas06 a { color: @tas06; &:hover { color: @tas06; } } .tas06-b { background: @tas06; }
.tas07, h6.tas07 a { color: @tas07; &:hover { color: @tas07; } } .tas07-b { background: @tas07; }
.tas08, h6.tas08 a { color: @tas08; &:hover { color: @tas08; } } .tas08-b { background: @tas08; }
.black, h6.black a { color: @black; &:hover { color: @black; } }

h6.elpa, .elpa-b { background: @tas09; }
h6.fisc, .fisc-b { background: @tas10; }
h6.lapr, .lapr-b { background: @tas11; }
