/* CONTENT
================================================== */
#content {
  
  .contenuto {
    &:after {
      content: "";
      line-height: 0;
      clear: both;
      display: block;
    }
    img {
      &.float {
        width: 220px;
        height: auto;
      }
      &.float-right {
        float: right;
        margin: 0 0 20px 20px;
      }
      &.float-left {
        float: left;
        margin: 0 20px 20px 0;
      }
    }
  }
  .important {
    img {
      &.float-left {
        width: 140px;
        height: auto;
      }
    }
  }
  
  #infinite-list {
    article {
      figure {
        margin-top: @baseDim / 2;
        .thumbnail {
          margin-bottom: 0;
        }
        figcaption {
          margin: 0;
          padding: @baseDim / 4;
          background: @salmone_d;
          font-size: @font-size - 4;
          text-align: right;
          span {
            font-weight: bold;
          }
        }
      }
    }
  }
  
  /* blocco dati borsa */
  #dati_borsa, #side-chart, #side-news, .side-article {
    header {
      padding-bottom: @baseDim / 2;
      h2 {
        line-height: 1em;
        margin: 0;
      }
    }
    p {
      margin: 0;
      line-height: 1em;
    }
  }
  #dati_borsa {
    ul {
      li {
        margin-top: @baseDim / 4;
        border-top: 1px solid @border_d;
        padding-top: @baseDim / 2;
        a {
          &:hover {
            text-decoration: none;
          }
          h5 {
            font-size: @font-size - 2;
            line-height: @font-size;
            color: @black;
            margin: 0;
            span {
              display: block;
              text-transform: none;
              font-weight: normal;
              font-size: @font-size - 6;
            }
            @media (min-width: 768px) and (max-width: 979px) {
              font-size: @font-size - 6;
              line-height: @font-size - 4;
              span {
                font-size: @font-size - 8;
              }
            }
          }
          .row-fluid [class*=span] {
            min-height: initial;
          }
          p {
            .montserrat();
            &.val, &.var {
              font-size: @font-size - 2;
              line-height: @font-size;
              text-align: right;
            }
            &.val {
              color: @black;
            }
            &.upd {
              font-size: @font-size - 8;
              line-height: @font-size;
              color: @brown;
              text-align: right;
            }
            @media (min-width: 768px) and (max-width: 979px) {
              &.val, &.var {
                font-size: @font-size - 6;
                line-height: @font-size - 4;
              }
              &.upd {
                font-size: @font-size - 8;
                span {
                  display: none;
                }
              }
            }
          }
        }
        @media (max-width: 767px) {
          height: 55px;
          position: relative;
          .data {
            position: absolute;
            top: @baseDim / 2;
            right: 0;
          }
        }
      }
    }
  }
  
  #side-chart {
    h3 {
      margin: 0;
    }
    article {
      position: relative;
      border-top: 1px solid @border_d;
      padding-top: @baseDim / 2;
      header {
        margin: 0;
      }
      .segnalibro {
        position: absolute;
        top: @baseDim / 2 - 2;
        right: 0;
      }
      .thumbnail {
        margin: 0;
      }
    }
  }
  
  .side-article {
    h3 {
      margin: 0;
    }
    article {
      position: relative;
      border-top: 1px solid @border_d;
      padding-top: @baseDim / 2;
      header {
        margin: 0;
      }
      p {
        line-height: 1.35em;
      }
      .segnalibro {
        position: absolute;
        top: @baseDim / 2 - 2;
        right: 0;
      }
      .thumbnail {
        margin: 0;
      }
    }
  }
  
  .side-list {
    h2 {
      margin-bottom: 0;
    }
    ul {
      background: @salmone_d;
      li {
        padding: @baseDim / 2;
        border-bottom: 1px solid @salmone;
      }
    }
    h6 {
      color: @primary;
      margin-bottom: @baseDim / 2;
    }
    h3 {
      font-size: @baseDim - 2;
      line-height: 1.15em;
      font-weight: 500;
      padding: 0;
      margin: 0;
    }
  }  
  #side-news {
    ul {
      li {
        margin-top: @baseDim / 4;
        border-top: 1px solid @border_d;
        padding-top: @baseDim / 2;
        article {
          h5 {
            font-size: @font-size - 6;
            span {
              margin-left: @baseDim - 12;
              padding-left: @baseDim - 12;
              text-transform: none;
              color: @brown;
              border-left: 2px solid @brown;
            }
          }
          h3 {
            .montserrat();
            font-size: 14px;
          }
        }
      }
    }
  }
  //.............................
  // ANIMAZIONE DI SIDE-NEWS !!!
  //.............................
  #infinite-list ol>li article.art-hashed {
    &:before {
      content: ' ';
      position: absolute;
      width: 100%;
      height: 100%;
      margin: -6px 0 0 -6px;
      padding: 6px;
      background: @hover;
      border-radius: 5px;
      opacity: 0;
      z-index: -1;
      -webkit-animation: fadeOut 1.8s ease-out 0;
              animation: fadeOut 1.8s ease-out 0;
    }
  }
  
  #sidebar section.newsletter .clearfix h3, #sidebar section.side-block .clearfix h3 {
    font-size: @font-size - 2;
    line-height: 1.2em;
    @media (min-width: 768px) and (max-width: 979px) {
      font-size: @font-size - 4;
      line-height: 1.3em;
    }
  }
  
}


@keyframes fadeOut {
  0% { opacity: 1 }
  100% { opacity: 0 }
}
@-webkit-keyframes fadeOut {
  0% { opacity: 1 }
  100% { opacity: 0 }
}



