        padding-top: @baseDim / 4;
      }
    }
  }
}



/* Form suggestions */
#content {
  #formSend {
    padding: @baseDim;
    margin-bottom: @baseDim * 2;
    
    h3 {
      margin-bottom: @baseDim * 2;
    }
    
    // Shared size and type resets
    select,
    textarea,
    input[type="text"],
    input[type="password"],
    input[type="datetime"],
    input[type="datetime-local"],
    input[type="date"],
    input[type="month"],
    input[type="time"],
    input[type="week"],
    input[type="number"],
    input[type="email"],
    input[type="url"],
    input[type="search"],
    input[type="tel"],
    input[type="color"],
    .uneditable-input {
      display: inline-block;
      padding: 5px 8px;
      margin-bottom: @baseLineHeight / 2;
      font-size: @baseFontSize;
      line-height: @baseLineHeight;
      vertical-align: middle;
      .montserrat();
      outline: none;
    }
    
    // Reset appearance properties for textual inputs and textarea
    // Declare width for legacy (cant be on input[type=*] selectors or its too specific)
    input,
    textarea,
    .uneditable-input {
      width: 236px; // plus 12px padding and 2px border
    }
    // Reset height since textareas have rows
    textarea {
      height: auto;
      border: none;
      resize: none;
      width: 220px;
    }
        
    // Set the height of select and file controls to match text inputs
    select,
    input[type="file"] {
      height: @inputHeight; /* In IE7, the height of the select element cannot be changed by height, only font-size */
      *margin-top: 4px; /* For IE7, add top margin to align select with labels */
      line-height: 1em;
      padding: 6px 0;
    }

    // Make select elements obey height by applying a border
    select {
      width: 220px; // default input width + 10px of padding that doesnt get applied
      border: 1px solid @inputBorder;
      background-color: @inputBackground; // Chrome on Linux and Mobile Safari need background-color
    }

    // Make multiple select elements height not fixed
    select[multiple],
    select[size] {
      height: auto;
    }

    
    // HORIZONTAL & VERTICAL FORMS
    // ---------------------------

    // Common properties
    // -----------------

    .form-search,
    .form-inline,
    .form-horizontal {
      input,
      textarea,
      select,
      .help-inline,
      .uneditable-input,
      .input-prepend,
      .input-append {
        display: inline-block;
        .ie7-inline-block();
        margin-bottom: 0;
        vertical-align: middle;
      }
      // Re-hide hidden elements due to specifity
      .hide {
        display: none;
      }
    }
    .form-search label,
    .form-inline label,
    .form-search .btn-group,
    .form-inline .btn-group {
      display: inline-block;
    }
    // Remove margin for input-prepend/-append
    .form-search .input-append,
    .form-inline .input-append,
    .form-search .input-prepend,
    .form-inline .input-prepend {
      margin-bottom: 0;
    }
    // Inline checkbox/radio labels (remove padding on left)
    .form-search .radio,
    .form-search .checkbox,
    .form-inline .radio,
    .form-inline .checkbox {
      padding-left: 0;
      margin-bottom: 0;
      vertical-align: middle;
    }
    // Remove float and margin, set to inline-block
    .form-search .radio input[type="radio"],
    .form-search .checkbox input[type="checkbox"],
    .form-inline .radio input[type="radio"],
    .form-inline .checkbox input[type="checkbox"] {
      float: left;
      margin-right: 3px;
      margin-left: 0;
    }


    // Margin to space out fieldsets
    .control-group {
      margin-bottom: @baseLineHeight / 2;
    }

    // Legend collapses margin, so next element is responsible for spacing
    legend + .control-group {
      margin-top: @baseLineHeight;
      -webkit-margin-top-collapse: separate;
    }

    // Horizontal-specific styles
    // --------------------------

    .form-horizontal {
      // Increase spacing between groups
      .control-group {
        margin-bottom: @baseLineHeight;
        .clearfix();
      }
      // Float the labels left
      .control-label {
        float: left;
        width: @horizontalComponentOffset - 20;
        padding-top: 5px;
        text-align: right;
      }
      // Move over all input controls and content
      .controls {
        // Super jank IE7 fix to ensure the inputs in .input-append and input-prepend
        // dont inherit the margin of the parent, in this case .controls
        *display: inline-block;
        *padding-left: 20px;
        margin-left: @horizontalComponentOffset;
        *margin-left: 0;
        &:first-child {
          *padding-left: @horizontalComponentOffset;
        }
      }
      // Remove bottom margin on block level help text since thats accounted for on .control-group
      .help-block {
        margin-bottom: 0;
      }
      // And apply it only to .help-block instances that follow a form control
      input,
      select,
      textarea,
      .uneditable-input,
      .input-prepend,
      .input-append {
        + .help-block {
          margin-top: @baseLineHeight / 2;
        }
      }
      // Move over buttons in .form-actions to align with .controls
      .form-actions {
        padding-left: @horizontalComponentOffset;
      }
    }
  }
}


