#content article#foglia.marketplace-detail {
  header {
    margin-bottom: @baseDim / 2;
    h1 {
      margin-bottom: @baseDim / 2;
      span {
        font-weight: normal;
        @media (min-width: 768px) and (max-width: 979px) {
          display: block;
        }
      }
    }
    p.code {
      color: @black;
      .sole_headline_regular();
      margin-bottom: @baseDim;
    }
    p.data {
      margin-bottom: @baseDim / 2;
      position: relative;
      text-align: right;
      .montserrat();
      span {
        color: @black;
        &.label {
          position: absolute;
          left: 0;
          @media (min-width: 980px) {
            left: 80px;
          }
          top: 50%;
          margin-top: -@baseDim / 2;
          line-height: @baseDim;
          font-size: @font-size - 4;
        }
        &.value {
          font-size: @font-size + 6;
        }
        &.good {
          color: @primary;
        }
        &.bad {
          color: @red;
        }
      }
    }
    p.upd {
      text-align: right;
      .montserrat();
      font-size: @font-size - 6;
    }
  }
  ul.index-list {
    padding: 0 @baseDim / 2;
    .montserrat();
    font-size: @font-size - 6;
    line-height: 1.2em;
    li {
      padding: @baseDim / 4 0;
      border-bottom: 1px solid @border;
      &.last {
        border: none;
      }
      a {
        padding: 2px 0;
        display: block;
        color: @black;
        font-weight: @bold;
        span {
          font-size: @font-size - 8;
          font-weight: normal;
          display: block;
        }
        &.active {
          color: @primary;
        }
      }
    }
  }
  .table {
    margin-top: @baseDim;
    border-top: 1px solid @border;
    padding-top: @baseDim;
    padding-bottom: @baseDim * 1.5;
    h3 {
      font-size: @font-size;
      line-height: 1.2em;
      margin-bottom: @baseDim * 1.5;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      .montserrat();
      li {
        position: relative;
        font-size: @font-size - 6;
        line-height: 1em;
        padding: 7px @baseDim / 2;
        margin: 0;
        overflow: hidden;
        &.md-odd {
          background: @salmone_d;
        }
        &.md-even {
          background: @salmone;
        }
        @media (max-width: 767px) {
          &.sm-odd {
            background: @salmone_d;
          }
          &.sm-even {
            background: @salmone;
          }
        }
        span {
          &.label {
            float: left;
          }
          &.value {
            float: right;
            font-weight: @bold;
          }
          &.good {
            color: @primary;
          }
          &.bad {
            color: @red;
          }
        }
      }
    }
  }
  p.more-data {
    a {
      font-size: @font-size - 2;
      text-decoration: underline;
    }
  }
}


