footer#footer {
  padding: @baseDim - 5 0;
  background: @primary;
  .sole-logo {
    float: left
  }
  ul {
    margin-top: 6px;
    float: right;
    border-left: 2px solid @primary_l;
    li {
      display: inline-block;
      margin-left: @baseDim + 4;
      .montserrat();
      font-size: @font-size - 4;
      text-transform: uppercase;
      color: @white;
      a {
        color: @white;
      }
    }
  }
}

@media (max-width: 767px) {
  footer#footer {
    text-align: center;
    
    .sole-logo {
      float: none
    }
    ul {
      margin-top: @baseDim - 5;
      float: none;
      border-left: none;
      li {
        &:first-child {
          margin-left: 0
        }
      }
    }
  }
}