#headerLogin:hover {
  .headerLogin-box {
    display: block;
  }
}
#headerLogin {
  position: relative;
  .trigger{
    position: relative;
    z-index: 99;
  }
  .headerLogin-box {
    display: none;
    .box-shadow(0 10px 10px rgba(0,0,0,0.2));
    padding: 0 @baseDim / 2;
    position:absolute;
    top: 15px;
    width: 200px;
    z-index: 9999;
    background: @white;
    right: 0;
    h6.welcome {
      font-size: @font-size - 6;
      text-align: left;
      padding: @baseDim @baseDim / 4 0;
      margin-bottom: @baseDim / 4;
      font-weight: normal;
      text-transform: none;
      span {
        font-weight: @bold;
      }
    }
    ul {
      padding: @baseDim / 4;
      li {
        text-align: left;
        padding: @baseDim / 4 0 @baseDim / 4;
        font-size: @font-size - 4;
        font-weight: @bold;
        border-bottom: 1px solid @hover;
        width: 100%;
        &:first-child {
          border-top: 1px solid @hover;
        }
        a {
          display: block;
          font-weight: normal;
          padding: @baseDim / 4 0;
          color: @black;
          &:hover {
            text-decoration: underline;
            color: @primary;
          }
          span {
            &.my {
              // font-weight: bold;
            }
            &.icon {
              color: @primary;
              display: inline-block;
              text-align: center;
              width: 14px;
              margin-right: @baseDim / 2;
              &:hover {
                text-decoration: none;
              }
            }
          }
        }
        &.logout {
          border: none;
          margin: 0;
          text-align: right;
          a {
            display: inline-block;
            color: @primary;
            font-size: @font-size - 7;
            text-decoration: underline;
          }
        }
      }
    }
  }
  form {
    text-align: left;
    padding: 20px 0 10px 0;
    line-height: 1em;
    
    input {
      .montserrat();
      font-size: @font-size - 4;
      outline: none;
      
      &[type="text"], &[type="password"] {
        height: 30px;
        width: 178px;
        padding: 2px 10px 0;
        border: 1px solid @hover;
        background: @white;
        margin-bottom: @baseDim / 2;
        color: @gray;
      }
      &[type="submit"] {
        height: 30px;
        width: 70px;
        .montserrat_bold();
        font-size: @font-size - 6;
        background: @primary;
        color: @white;
        border: none;
        .border-radius(0);
        padding-top:0;
        cursor: pointer;
        text-transform: uppercase;
      }
    }
  
    .stay-logged {
      margin-bottom: @baseDim - 5;
      input {
        margin-right: 4px;
      }
      .checkbox {
        .montserrat();
        font-size: @font-size - 7;
        color: @black;
        cursor: pointer;
        padding: 2px 0;
        display: inline-block;
      }
    }
  
    .submit-forgot {
      overflow: hidden;
      position: relative;
      margin-bottom: @baseDim;
      .submit {
        float: none;
        margin-bottom: 0;
      }
    }
  
    a.forgot-password {
      .montserrat();
      font-size: @font-size - 7 !important;
      padding: 4px 0;
      display: inline-block;
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -10px;
      &:hover {
        text-decoration: underline;
      }
    }
  
    .social {
      overflow: hidden;
      background: @carta;
      padding: 7px 5px;
      margin: 0 -10px @baseDim / 2;
      h6 {
        color: @black;
        padding: 0 5px 5px;
        margin: 0;
        .montserrat();
        font-size: @font-size - 7 !important;
      }
      a {
        padding: 0;
        margin: 5px;
        img.ho {
          display: none;
        }
      }
      .notouch & {
        a:hover {
          img {
            &.no {
              display: none;
            }
            &.ho {
              display: inline;
            }
          }
        }
      }
    }
  
    .sign-up {
      a {
        .montserrat();
        font-size: @font-size - 7 !important;
        display: inline-block;
        padding: 4px 0;
        color: @black !important;
        text-decoration: none;
        span {
          color: @primary !important;
          text-decoration: underline;
        }
      }
    }
  }
}