.ui-datepicker {
  padding:15px;
  display:none;
  background:@white;
  color:@primary;
  font-weight:bold;
  font-size:14px;
  text-transform:uppercase;
  z-index:10;
  box-shadow:0 0 3px #333;
  left:50% !important;
  top:50%;
  margin-top:@baseDim / 4;
  a {
    color:@primary;
  }
  .ui-datepicker-header {
    position:relative;
    padding:0;
    margin-bottom:3px;
    border-bottom:1px solid @hover;
    padding-bottom:5px
  }
  .ui-datepicker-prev,
  .ui-datepicker-next {
    position:absolute;
    width:5px;
    height:13px;
    cursor:pointer;
    font-family: 'icomoon';
    speak:none;
    font-size:10px;
    font-style:normal;
    font-weight:normal;
    font-variant:normal;
    text-transform:none;
    line-height:1;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
    &:hover {
      text-decoration:none;
    }
  }
  .ui-datepicker-prev:before {
    content:"\e605";
  }
  .ui-datepicker-next:before {
    content:"\e606";
  }
  .ui-datepicker-title {
    margin:0 0 0px 0;
    line-height:1;
    font-size:14px;
    text-align:center;
    .montserrat_bold();
    select {
      font-size:1em;
      margin:1px 0;
    }
  }
  select.ui-datepicker-month-year {
    width:100%;
  }
  select.ui-datepicker-month, select.ui-datepicker-year {
    width:49%;
  }
  table {
    width:100%;
    font-size:.9em;
    border-collapse:collapse;
    margin:0;
    border-top:1px solid @hover;
  }
  th {
    padding:3px 0;
    text-align:center;
    border:0;
    .montserrat_bold();
    border-bottom:1px solid @hover;
  }
  td {
    border:0;
    padding:1px;
    a.ui-state-active {
      background:@primary;
      color:@white;
    }
  }
  .ui-datepicker-buttonpane {
    background-image:none;
    margin:.7em 0 0;
    padding:0 .2em;
    border-left:0;
    border-right:0;
    border-bottom:0;
    button {
      float:right;
      margin:.5em .2em .4em;
      cursor:pointer;
      padding:.2em .6em .3em;
      width:auto;
      overflow:visible;
    }
    button.ui-datepicker-current {
      float:left;
    }
  }
  .calDisable {
    color:#999;
  }
}
.ui-datepicker th,.ui-datepicker td {
  padding-bottom:5px;
}
.ui-datepicker td span,.ui-datepicker td a {
  display:block;
  padding:.2em;
  text-align:center;
  text-decoration:none;
  font-size: 14px;
  .montserrat();
}
.ui-datepicker.ui-datepicker-multi {
  width:auto;
}
.ui-datepicker-multi {
  .ui-datepicker-group {
    float:left;
    table {
      width:95%;
      margin:0 auto .4em;
    }
  }
  .ui-datepicker-buttonpane {
    clear:left;
  }
}
.ui-datepicker-multi-2 {
  .ui-datepicker-group {
    width:50%;
  }
}
.ui-datepicker-multi-3 {
  .ui-datepicker-group {
    width:33.3%;
  }
}
.ui-datepicker-multi-4 {
  .ui-datepicker-group {
    width:25%;
  }
}
.ui-datepicker-multi .ui-datepicker-group-last .ui-datepicker-header,.ui-datepicker-multi .ui-datepicker-group-middle .ui-datepicker-header {
  border-left-width:0;
}
.ui-datepicker-row-break {
  clear:both;
  width:100%;
  font-size:0;
}
.ui-datepicker-rtl {
  direction:rtl;
  .ui-datepicker-prev {
    right:1px;
    left:auto;
  }
  .ui-datepicker-next {
    left:1px;
    right:auto;
  }
  .ui-datepicker-buttonpane {
    clear:right;
    button {
      float:left;
    }
  }
}
.ui-datepicker-rtl .ui-datepicker-buttonpane button.ui-datepicker-current,.ui-datepicker-rtl .ui-datepicker-group {
  float:right;
}
.ui-datepicker-rtl .ui-datepicker-group-last .ui-datepicker-header,.ui-datepicker-rtl .ui-datepicker-group-middle .ui-datepicker-header {
  border-right-width:0;
  border-left-width:1px;
}
.ui-datepicker-prev.ui-state-disabled,.ui-datepicker-next.ui-state-disabled {
  display:none;
}
.ui-datepicker-trigger {
  display:none;
}
