/* RESET
================================================== */
@import "yui_reset.less";
nav,menu{
  list-style:none;
  margin:0;
  padding:0;
}

/* BOOTSTRAP
================================================== */
@import "bootstrap/bootstrap.less";
@import "bootstrap/responsive.less";

/* GLOBAL
================================================== */
* {
	-webkit-tap-highlight-color:rgba(0,0,0,0);
}


/* CALENDARIO SCADENZE
================================================== */
@import "scadenze-calendar.less";


/* FONT
================================================== */
@import "icomoon/icomoon.less";
@book:      300;
@regular:   400;
@bold:      700;
@extrabold: 800;
@_light: 300;
@_regular: 400;
@_medium: 500;
@_bold: 700;
@_black: 800;
@_extrablack: 900;
@sole_headline: "sole_headline", Georgia, Times, "Times New Roman", serif;
.sole_headline_bold() {
  font-family: @sole_headline;
  font-weight: @bold;
}
.sole_headline_regular() {
  font-family: @sole_headline;
  font-weight: @regular;
}
@sole_text: "sole_text", Georgia, Times, "Times New Roman", serif;
.sole_text() {
  font-family: @sole_text;
  font-weight: @regular;
}
.sole_text_bold() {
  font-family: @sole_text;
  font-weight: @bold;
}
.sole_text_italic() {
  font-family: @sole_text;
  font-weight: @regular;
  font-style: italic;
}
@montserrat: "Montserrat", Arial, "Helvetica Neue", Helvetica, sans-serif;
.montserrat() {
  font-family: @montserrat;
  font-weight: @regular;
}
.montserrat_bold() {
  font-family: @montserrat;
  font-weight: @bold;
}


/* DEFINIZIONE VARIABILI
================================================== */
@baseImageUrl: "../img";
@baseDim: 20px;
@shadow: 0 0 8px rgba(0,0,0,.35);
/* font */
@font-size: 18px;


/* THEME
================================================== */
@import "theme.less";


/* DATEPICKER (preso dal Sole24Ore)
================================================== */
@import "datepicker.less";


/* GLOBAL
================================================== */
body {
  .sole_text();
  font-size: @font-size - 2;
  // background: @salmone_d;
  background: @salmone;
  color: @black;
}
.container {
  margin: 0 auto;
  padding: 0 @baseDim;
  position: relative;
  // background: @salmone;
  // .box-shadow(@shadow);
}
a {
  text-decoration: none;
  color: @link;
  outline: 0 none;
  &:hover {
    text-decoration: underline;
    color: @link;
  }
}
h1, h2, h3, h4, h5, h6 {
  line-height: 1em;
  margin-bottom: 0.6em;
  font-weight: normal;
}
h1 {
  .sole_headline_bold();
  font-size: @font-size * 2 - 1;
}
h2 {
  .sole_text();
  line-height: 1.35em;
  font-size: @font-size + 6;
  color: @gray;
}
h3 {
  .sole_headline_bold();
  font-size: @font-size + 6;
  a {
    color: @black;
  }
}
h4 {
  .montserrat_bold();
  font-size: @font-size;
  text-transform: uppercase;
}
h5 {
  .montserrat_bold();
  font-size: @font-size - 4;
  text-transform: uppercase;
}
h6 {
  .montserrat_bold();
  font-size: @font-size - 6;
  text-transform: uppercase;
  &.ext {
    .sole_text();
    font-size: @font-size - 4;
    text-transform: none;
  }
  time {
    font-size: @font-size - 6;
    margin-left: 5px;
    padding-left: 5px;
    border-left: 1px solid @border;
    color: @brown;
    text-transform: none;
    font-weight: @regular;
  }
}
p, span.txt {
  .sole_text();
  margin-bottom: 0.6em;
  line-height: 1.35em;
  small {
    font-size: @font-size - 4;
  }
  b, strong {
    font-weight: @bold;
  }
  i, em {
    font-style: italic;
  }
}
hr {
  border: 0;
  height: 1px;
  background: @border;
  margin: @baseDim / 2 0;
}
span.separator {
  display: block;
  height: @baseDim;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.well {
  background: @salmone_d;
  padding: @baseDim / 2;
}
.well-large {
  margin: 0 -@baseDim;
  padding: @baseDim;
}
.well-inverse {
  background: @salmone;
  padding: @baseDim;
}
.well-white {
  background: @white;
}
.well-light {
  padding: @baseDim;
  background: @salmone_c;
}
.icon-star {
  color:@primary !important;
}
.icon-star-empty {
  color:@brown !important;
  &:hover {
    color:@primary !important;
  }
}
.thumbnail {
  margin-bottom: @baseDim;
}
.fixed-top {
  position: fixed;
  top: 0;
}
figure {
  margin: 0;
}


.btn-qdf {
  display:inline-block;
  padding:6px 8px;
  color:@primary;
  background-color:@salmone_d;
  font-size:12px;
  .montserrat_bold();
  text-align:center;
  text-decoration:none !important;
  a {
    color:@white;
  }
  &.active {
    color:@white;
    background-color:@primary_d ;
  }
}
.btn-qdf-m {
  background: @primary;
  color: @white;
  text-transform: uppercase;
  &:hover {
    color: @white;
  }
}
.day {
  .meta {
    background:@salmone_d;
    border-left: 1px solid @salmone_d;
    margin: 0;
    padding: 6px @baseDim / 2;
    color: @white;
    font-weight: @bold;
    text-align: center;
    a {
      color: @black;
    }
  }
  time {
    padding: @baseDim @baseDim / 2;
    background: @carta;
    position: relative;
    border-bottom: 2px solid @salmone_d;
    border-left: 1px solid @salmone_d;
    &:before {
      content: "";
      display: block;
      width: 0; 
      height: 0; 
      border-right: 12px solid @salmone_d; 
      border-top: 12px solid transparent;
      position: absolute;
      bottom: 0;
      right: 0;
    }
    &:after {
      content: "";
      display: block;
      width: 0; 
      height: 0; 
      border-left: 12px solid @white; 
      border-bottom: 12px solid transparent;
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}
.sticky {
  .day {
    margin-bottom: @baseDim / 2;
  }
}
time.date {
  .sole_headline_bold();
  overflow: hidden;
  display: block;
  span {
    display: block;
    &.g {
      font-size: 50px;
      line-height: 1em;
      float: left;
      width: 50%;
      text-align: right;
    }
    &.ma {
      padding-top: 5px;
      font-size: 18px;
      line-height: 20px;
      float: right;
      width: 42%;
    }
  }
}
.file {
  position: absolute;
  top: 0;
  left: 3px;
  display: block;
  height: 36px;
  width: 28px;
  background: @carta;
  border-bottom: 2px solid @salmone_d;
  border-left: 1px solid @salmone_d;
  span {
    display: inline-block;
    margin-left: -3px;
    margin-top: 8px;
    font-size: @font-size - 10;
    line-height: 1em;
    padding: 3px 2px 1px;
    text-transform: uppercase;
    .montserrat_bold();
    color: @white;
    background: @gray;
    &:before {
      content: "";
      display: block;
      width: 0; 
      height: 0; 
      border-top: 6px solid @salmone; 
      border-left: 6px solid transparent;
      position: absolute;
      top: 0;
      left: 22px;
    }
    &:after {
      content: "";
      display: block;
      width: 0; 
      height: 0; 
      border-bottom: 6px solid @salmone_d; 
      border-right: 6px solid transparent;
      position: absolute;
      top: 0;
      left: 22px;
    }
    &.pdf { background: #d90e0e; }
    &.doc { background: #5f82b4; }
    &.xls { background: #2d8f2d; }
    &.ppt { background: #c75c00; }
    &.zip { background: #d9ba0e; }
    &.ics { background: #9d3334; }
  }
}


/* HEADER
================================================== */
//@import "header_old.less";
@import "header.less";


/* CONTENT
================================================== */
#content {
  padding-top: @baseDim;
  padding-bottom: @baseDim * 2;
  ul.list {
    li {
      padding-left: @baseDim;
      position: relative;
      margin: @baseDim / 4 0;
      font-size: @font-size - 4;
      .icon-caret-right {
        color: @primary;
        font-size: @font-size - 4;
        position: absolute;
        top: 3px;
        left: 0;
      }
      span.tipo {
        font-size: @font-size - 6;
        text-transform: uppercase;
        .montserrat_bold();
        margin-right: @baseDim / 4;
      }
      a {
        color: @black;
      }
    }
  }
  a.segnalibro:hover {
    text-decoration: none;
  }
  
  /* FORM */
  form {
    .input-append,
    .input-prepend {
      display: inline-block;
      margin-top: @baseDim;
      margin-bottom: @baseDim / 2;
      vertical-align: middle;
      font-size: 0; // white space collapse hack
      white-space: nowrap; // Prevent span and input from separating
      // Reset the white space collapse hack
      input,
      select {
        font-size: @baseFontSize;
        position: relative; // placed here by default so that on :focus we can place the input above the .add-on for full border and box-shadow goodness
        margin-bottom: 0; // prevent bottom margin from screwing up alignment in stacked forms
        *margin-left: 0;
        vertical-align: top;
      }
    }
    button,
    input,
    select {
      display: inline-block;
      outline: none;
      padding: 8px 10px 6px;
      font-size: @font-size - 6;
      line-height: 1em;
      border: none;
      margin: 0;
      box-sizing: border-box;
      //height: 32px;
      .border-radius(0);
    }
    button {
      height: 32px;
      background: @primary;
      color: @white;
      .montserrat_bold();
      text-transform: uppercase;
      cursor: pointer;
    }
    input,
    select {
      .montserrat();
    }
  }
  
  .well {
    h2, h2 a {
      color: @primary !important;
      font-size: @font-size;
    }
    p.more {
      border-top: 1px solid @border_d;
      a {
        .montserrat_bold();
        font-size: @font-size - 6;
        text-transform: uppercase;
      }
    }
  }

  
  
  /* SIDEBAR */
  #sidebar {
    border-top: 1px solid @border;
    padding-top: @baseDim / 2;
    &:before {
      content: "";
      display: block;
      height: 100%;
      width: 1px;
      background: @border;
      position: absolute;
      top: 0;
      left: 650px;
    }
    section {
      padding-bottom: @baseDim;
      border-bottom: 1px solid @border;
      margin-bottom: @baseDim;
      h2, h2 a {
        font-weight: @bold;
        color: @black;
      }
      p {
        margin-bottom: 0;
        &.more {
          padding-top: @baseDim / 2;
          font-size: @font-size - 4;
          line-height: @font-size - 4;
          a:hover {
            text-decoration: none;
          }
          .icon {
            margin-right: @baseDim / 2;
            font-size: @font-size - 4;
            line-height: @font-size - 4;
          }
        }
      }
      &.adv {
        padding-bottom: @baseDim / 2;
        img {
          display: block;
        }
      }
      &.categorie, &.lancio {
        text-align: center;
        a:hover {
          text-decoration: none;
        }
        h2 {
          .montserrat_bold();
          font-size: @baseDim;
          text-transform: uppercase;
          color: @primary;
        }
        p {
          color: @black;
        }
      }
      &.quesito, &.wbg {
        article {
          padding: @baseDim 0;
          position: relative;
          h6 {
            margin-top: 2px;
            margin-bottom: @baseDim / 2;
          }
          a.segnalibro {
            position: absolute;
            top: @baseDim;
            right: 0;
          }
        }
      }
      &.analisi, &.speciale {
        // margin-top: -@baseDim / 2;
        h2 {
          margin-bottom: 0;
        }
        p.di {
          margin-bottom: @baseDim / 2;
          color: @brown;
          .autore {
            color: @black;
            .sole_text_italic();
          }
        }
        article {
          border-top: 1px solid @border;
          padding-top: @baseDim / 2;
          position: relative;
          h6 {
            margin-top: 2px;
            margin-bottom: @baseDim / 2;
          }
          a.segnalibro {
            position: absolute;
            top: @baseDim / 2;
            right: 0;
          }
        }
      }
      &.newsletter, &.side-block {
        .clearfix {
          img {
            float: left;
            margin-right: @baseDim / 2;
            margin-bottom: @baseDim / 2;
          }
          h3 {
            color: @primary;
            font-size: @font-size;
            line-height: @font-size + 4;
            padding-right: @baseDim * 1.5;
            margin-left: @baseDim * 4;
            a {
              color: @primary;
            }
          }
          p {
            font-size: @font-size - 2;
            color: @gray;
            margin-bottom: @baseDim / 2;
            &:before {
              display: table;
              content: "";
              line-height: 0;
              clear: both;
            }
          }
        }
        input {
          width: 200px;
        }
      }
    }
  }
  
  /* INFINITE LIST */
  .relative {
    position: relative;
  }
  #ordinamento {
    display:block;
    position:relative;
    //top:5px;
    padding: 15px @baseDim / 2 @baseDim;
    height: 25px;
    text-align: right;
    z-index: 3;
    .trigger{
      //top:5px;
    }
    a {
      position:relative;
      .montserrat();
      font-size: @font-size - 4;
      text-decoration:none;
    }
    .filtro-ricerca {
      top:35px;
      display:none;
      margin-top:@baseDim / 4 -@baseDim / 2 0 0;
      width:auto;
      position:absolute;
      right:0;
      background:@white;
      .box-shadow(0 0 3px 0px #999);
      li {
        padding:@baseDim - 12 @baseDim + 2 @baseDim - 12 @baseDim + 10;
        &:hover {
          background:@hover;
        }
      }
    }
  }
  #infinite-list {
    border-top: 1px solid @border;
    position: relative;
    &.con-ordinamento {
      margin-top:-60px;      
    }
    h5 {
      display: block;
      z-index: 2;
      text-align: center;
      margin: 0;
      padding: @baseDim / 2 0 @baseDim;
      height: 25px;
      text-transform: none;
      color: @primary;
      .sole_headline_bold();
      font-size: @font-size;
      line-height: 1.5em;
      #gradient > .vertical(@salmone_d, @salmone);
      border-bottom: @baseDim / 2 solid @border;
    }
    ol {
      > li {
        padding: @baseDim 0;
        margin-top: -1px;
        border-top: 1px solid @border;
        border-bottom: 1px solid @border;
        article {
          position: relative;
          a.segnalibro {
            position: absolute;
            top: 0;
            right: @baseDim / 4 * 3;
          }
          header {
            h6 {
              margin-top: 2px;
              margin-bottom: @baseDim / 2;
            }
            h3 {
              margin-bottom: @baseDim / 4;
            }
          }
          time.date {
            padding-top: @baseDim / 2;
            padding-bottom: @baseDim / 2;
          }
          p {
            margin-bottom: 0;
          }
          footer {
            margin-top: @baseDim / 2;
            // border-top: 1px solid @border;
            // padding-top: @baseDim / 2;
            .montaggio {
              article {
                header {
                  min-height: initial;
                  padding: 0;
                  h3 {
                    font-weight: @_medium;
                    font-size: @font-size;
                    line-height: @font-size + 2;
                  }
                }
              }
            }
          }
        }
        &.quesito, &.analisi, &.wbg {
          .well {
            padding: 10px 15px;
          }
          h2 {
            font-weight: bold;
            margin-bottom: @baseDim;
            overflow: hidden;
            span {
              color: @primary;
            }
          }
          a.segnalibro {
            right: 0;
          }
        }
        &.analisi {
          h2 {
            margin-bottom: 0;
          }
          a.segnalibro {
            top: @baseDim / 2;
          }
          p.di {
            margin-bottom: @baseDim / 2;
            color: @brown;
            .autore {
              color: @black;
              .sole_text_italic();
            }
          }
          article {
            border-top: 1px solid @border;
            padding-top: @baseDim / 2;
          }
        }
        &.important {
          border-bottom: @baseDim / 2 solid @border;
          article {
            header {
              margin-bottom: @baseDim;
              h3 {
                font-size: @font-size * 2 - 1;
              }
            }
          }
        }
      }
    }
    .fotonotizia {
      .well {
        header {
          h3 {
            font-style: italic;
            font-weight: normal;
          }
          p {
            color: @black;
            .autore {
              font-style: normal;
              font-weight: bold;
              text-transform: uppercase;
            }
          }
        }
        figure {
          margin: 0;
          padding: 0;
          .thumbnail {
            margin: 0;
            position: relative;
            span {
              position: absolute;
              bottom: @baseDim / 4;
              right: @baseDim / 4;
              color: @white;
              .opacity(75);
              font-size: @baseDim * 2.5;
            }
          }
        }
        a.segnalibro {
          right: 0;
        }
      }
    }
    .scroll-foto {
      .well {
        background: @salmone_c;
        figure {
          margin-top: 0;
        }
        li {
          float: left;
        }
        article {
          h6 {
            padding-bottom: @baseDim / 2;
            border-bottom: 1px solid @border;
          }
          h3 {
            .sole_headline_regular();
            font-style: italic;
            font-size: @font-size + 8;
          }
        }
        .pagination {
          margin-top: @baseDim - 5;
          border-top: 1px solid @border;
          padding-top: @baseDim / 2;
          text-align: center;
          a {
            display: inline-block;
            height: @baseDim / 2 + 2;
            width: @baseDim / 2 + 2;
            margin: 0 @baseDim / 5;
            border: 1px solid @border;
            .border-radius(50%);
            &.selected {
              background: @salmone_d;
            }
            span {
              display: none;
            }
          }
        }
      }
    }
  }
  .add-result {
    position: relative;
    padding: @baseDim * 2 0 0;
    a {
      span{
        &.icon-plus {
          border-left: 1px solid @white;
          padding: 0 @baseDim / 4 0 @baseDim / 2;
          margin-left: @baseDim / 4;
        }
        &.icon-time-machine {
          font-size: 40px;
        }
      }
      &.trigger {
        width: auto;
        text-decoration: none;
      }
      &:hover {
        text-decoration: none;
        span.icon-time-machine {
          color: @primary_d;
        }
      }
    }
    .intervallo-tempo {
      bottom: 60px;
      left: 50%;
      margin-left: -80px;
      display: none;
      width: 160px;
      position: absolute;
      background: @white;
      .box-shadow(0 0 3px 0px #999);
      &:before {
        content: "";
        display: block;
        position: absolute;
        bottom: -10px;
        left: 50%;
        margin-left: -10px;
        border-style: solid;
        border-width: 10px 10px 0 10px;
        border-color: @white transparent transparent transparent;
        
      }
      li {
        padding: @baseDim - 12 0;
        .montserrat_bold();
        font-size: @font-size - 6;
        text-transform: uppercase;
        &:hover {
          background:@hover;
        }
        a {
          display: block;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
  }
  
  article {
    header {
      margin-bottom: @baseDim / 2;
      p {
        color: @brown;
        margin: 0;
        .autore {
          color: @black;
          .sole_text_italic();
        }
      }
    }
    .allegati {
      ul {
        li {
          padding-top: @baseDim / 2;
          .montserrat_bold();
          font-size: @font-size - 6;
          line-height: 1.3em;
          min-height: 40px;
          overflow: hidden;
          a {
            display: inline-block;
            padding: 2px 0 2px @baseDim * 2;
            position: relative;
            min-height: @baseDim * 2;
            text-decoration: underline;
            color: @black;
          }
        }
      }

      &.multimedia {
        padding-bottom: @baseDim;
        ol {
          li {
            min-height: 0;
            line-height: 1em;
            color: @primary;
            .opacity(50);

            &.current,&:hover {
              .opacity(100);
            }
            a {
              position: relative;
              min-height: 0;
              padding: 0;
              .montserrat();
              font-size: 12px;
              text-decoration: none;
              color: @primary;
              margin: 0 0 @baseDim / 2;
              &:hover {
                text-decoration: underline;
              }
              
              img {
                margin: 0;
                padding: 0;
              }
              
              &.thumbnail-video {
                span {
                  position: absolute;
                  top: 50%;
                  left: 50%;
                  display: block;
                  width: 30px;
                  padding-left: 2px;
                  height: 32px;
                  margin: -16px 0 0 -20px;
                  text-align: center;
                  font-size: 16px;
                  line-height: 32px;
                  color: @white;
                  background: @black;
                  background: rgba(0,0,0,.4);
                  border-radius: 50%;
                }
              }
            }
          }
        }
      }
    }
  }
  
  .side_bordered {
    .well-light {
      padding: @baseDim @baseDim / 4;
      border-top: @baseDim / 2 solid @primary;
      font-size: @font-size - 4;
      line-height: 1.2em;
      ul {
        margin: -@baseDim / 2 @baseDim / 4;
        li {
          color: @primary;
          padding-left: @baseDim / 2;
          line-height: 1.5em;
          a {
            color: @salmone_d;
            &:hover, &.active {
              color: @primary;
            }
          }
        }
      }
      strong {
