        text-transform: uppercase;
        text-align: center;
        .montserrat();
        font-size: @font-size - 8;
        line-height: 1.2em;
        .border-top-radius(2px);
        padding: 3px 0 0;
        &:after {
          content: '';
          display: block;
          width: 0;
          height: 0;
          border-left: @baseDim / 2 solid transparent;
          border-right: @baseDim / 2 solid transparent;
          border-bottom: @baseDim / 2 solid @salmone;
          margin-top: 1px;
