/* MODAL GALLERY (colorbox)
================================================== */
/*
    Colorbox Core Style:
    The following CSS is consistent between example themes and should not be altered.
*/
#colorbox, #cboxOverlay, #cboxWrapper{position:absolute; top:0; left:0; z-index:9999; }//overflow:hidden;}
#cboxWrapper {max-width:none;}
#cboxOverlay{position:fixed; width:100%; height:100%;}
#cboxMiddleLeft, #cboxBottomLeft{clear:left;}
#cboxContent{position:relative;}
#cboxLoadedContent{overflow:auto; -webkit-overflow-scrolling: touch;}
#cboxTitle{margin:0;}
#cboxLoadingOverlay, #cboxLoadingGraphic{position:absolute; top:0; left:0; width:100%; height:100%;}
#cboxPrevious, #cboxNext, #cboxClose, #cboxSlideshow{cursor:pointer;}
.cboxPhoto{float:left; margin:auto; border:0; display:block; max-width:none; -ms-interpolation-mode:bicubic;}
.cboxIframe{width:100%; height:100%; display:block; border:0;}
#colorbox, #cboxContent, #cboxLoadedContent{box-sizing:content-box; -moz-box-sizing:content-box; -webkit-box-sizing:content-box;}

/* 
    User Style:
    Change the following styles to modify the appearance of Colorbox.  They are
    ordered & tabbed in a way that represents the nesting of the generated HTML.
*/
#cboxOverlay {
  background:#fff;
}

#colorbox {
  outline:0;
  
  #cboxContent {
    overflow:visible;
    background:#fff;

    h1#cboxCustomTitle {
      position: absolute;
      bottom: 100%;
      left: 0;
      color: @primary;
      font-size: 24px;
    }

    #cboxError {
      padding:50px;
      border:1px solid #ccc;
    }
    #cboxLoadedContent {
      background:#fff;
    }
    #cboxLoadingGraphic {
      display: none
    }
    #cboxLoadingOverlay {
      background:#fff;
    }
    #cboxTitle {
      position:absolute;
      width: 100%;
      padding-top: 10px;
      left:0;
      color:#000;
      
      .cbox-dida {
        position: relative;
        padding-right: 60px;
        .montserrat();
        font-size: @font-size - 4;

        .cbox-credits {
          margin-left: @baseDim;
          font-size: @font-size - 6;
          color: @gray;
        }
        .cbox-pagination {
          position: absolute;
          right: 0;
          top: 10px;
          .sole_headline_bold();
          font-size: 16px;
          color: @black;
        }
      }
    }

    #cboxPrevious, & #cboxNext {
      position: absolute;
      margin: -15px 0 0 0;
      padding: 0;
      top: 50%;
      left: 0;
      background: none;
      border: none;
      outline:0;
      z-index: 3;

      &:hover  {
        span {
          background: @primary;
        }
      }

      span {
        display: block;
        width: 30px;
        height: 26px;
        padding: 5px 0 0 0;
        float: none;
        font-size: 20px;
        text-align: center;
        text-decoration: none;
        line-height: 1;
        color: @white;
        background: @primary;
        background: rgba(65,95,118,.5);
      }
    }
    #cboxNext {
      width: 100%;
      height: 100%;
      margin: 0;
      top: 0;
      text-align: right;
      z-index: 2;

      span {
        float: right;
      }
    }
    
    #cboxSlideshow, & #cboxClose {
      position: fixed;
      bottom: 20px;
      .square(30px);
      padding:2px 0 0 0;
      margin:0;
      font-size: 16px;
      background: @primary;
      color: @white;
      border: none;
      outline:0;
    }

    #cboxClose {
      padding: 0;
      right: 20px;
      top: 20px;
      background: @primary;
      color: @white;
      border: none;
    }
    #cboxCurrent {
      display: none !important;
    }

    &.vertical {
      #cboxTitle {
        width: 200px;
        height: auto;
        bottom: 0;
        float: right;
        left: auto;
        right: -220px;
        
        .cbox-dida {
          .cbox-credits, & .cbox-pagination {
            display: block;
            margin: @baseDim / 2  0 0 0;
            float: none;
            clear: both;
          }
          .cbox-pagination {
            position: relative;
            top: 0;
          }
        }
      }
    }
  }
}


/* SIDENAV
================================================== */
#sidenav {
  a {
    display: block;
    text-align: center;
    padding: @baseDim * 3.5 0;
    width: 30px;
    font-size: @font-size * 2;
    color: @white;
    background: @primary_d;
    position: fixed;
    top: 300px;
    @reveal: padding 0.3s, background 0.3s;
    .transition(@reveal);
    &.prev_post {
      left: 0;
      &:hover {
        padding-left: 20px;
      }
    }
    &.next_post {
      right: 0;
      &:hover {
        padding-right: 20px;
      }
    }
    &:hover {
      text-decoration: none;
      background: @primary;
    }
  }
}
@media (min-width: 980px) and (max-width: 1000px) { 
  #sidenav {
    a {
      width: 20px;
    }
  }
}




/* ADV
================================================== */
.top_banner {
  padding: @baseDim / 2 0;
  background: @salmone_d;
  .wrapper {
    width: 728px;
    margin: 0 auto;
  }
}


