/* RESPONSIVE
================================================== */
/* Responsive: Large desktop
-------------------------------------------------- */
@media (min-width: 1200px) {
}
/* Responsive: Tablet to desktop
-------------------------------------------------- */
@media (min-width: 768px) and (max-width: 979px) {  
  header#header {
    ul.second_header {
      position: relative;
      padding-bottom: @baseDim * 1.5;
      time {
        position: absolute;
        width: 100%;
        text-align: center;
        bottom: 0;
        left: 0;
      }
      li {
        border: none;
        &:first-child {
          padding: 0;
        }
        &.last {
          border-left: 1px solid @border;
        }
      }
    }
  }
  
  .span2ext {
    width: 140px; /* Simula uno span3 */
  }
  .offset2ext {
    margin-left: 180px; /* Simula uno offset3 */
  }
  
  h2 {
    font-size: @font-size + 4;
  }
  #content {
    #sidebar {
      &:before {
        left: 506px;
      }
      section {
        &.newsletter, &.side-block {
          .clearfix {
            h3 {
              font-size: @font-size - 2;
              line-height: @font-size;
              padding-right: 0;
            }
            p {
              font-size: @font-size - 4;
              line-height: 1.35em;
            }
          }
          input {
            width: 145px;
          }
        }
      }
    }
    article#foglia {
      .opzioni {
        margin-left: -@baseDim / 4;
        margin-right: -@baseDim / 4;
        font-size: @font-size - 8;
        a {
          display: block;
        }
      }
    }

    /* blocco abbonati */
    #abbonati {
      .alert-abbonati {
        padding: @baseDim;
        p {
          // font-size: @font-size - 4 !important;
        }
      }
      a.super-abbonati {
        span.txt {
          font-size: @font-size - 6;
        }
      }
      .accedi {
        p {
          // font-size: @font-size - 6 !important;
        }
        form {
          input {
            width: 120px;
          }
        }
      }
    }
    .row-sep {
      article {
        &.sep2:before {
          left: 196px;
        }
        &.sep3:before {
          left: 382px;
        }
      }
    }
  }
}


@media (max-width: 979px) {
  h6 time {
    display: block;
    margin-top: @baseDim / 4;
    border: 0;
    margin-left: 0;
    padding-left: 0;
  }

  .table-wrap {
    width: 100%;
    height: auto;
    &.large {
      position: relative;
      border-left: 1px solid @border;
      border-right: 1px solid @border;
      margin: 0 -1px @baseDim;
      &:before {
        content: ' ';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: @baseDim / 4 * 3;
        height: 100%;
        @transparent: rgba(243,226,209,0);
        #gradient > .horizontal(@salmone, @transparent);
        background-color: transparent;
        z-index: 1;
      }
      &:after {
        content: ' ';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: @baseDim / 4 * 3;
        height: 100%;
        @transparent: rgba(243,226,209,0);
        #gradient > .horizontal(@transparent, @salmone);
        background-color: transparent;
        z-index: 1;
      }
    }
    .table-content {
      width: 100%;
      height: auto;
      overflow: scroll;
      -webkit-overflow-scrolling: touch;
      table {
        margin: 0;
      }
    }
  }
  #infinite-list, #calendar {
    .day .meta {
      padding: @baseDim / 2 0;
      text-align: center;
    }
    time.date {
      padding: @baseDim / 4 0;
      span {
        &.g, &.ma {
          float: none;
          text-align: center;
          width: 100%;
          padding: 0;
        }
      }
    }
  }
  #sidenav {
    display: none;
    a {
      padding: 0;
      position: fixed;
      top: auto;
      bottom: 0;
      width: 30px;
      height: 90px;
      line-height: 100px;
      font-size: 30px;
      padding: 4px 0 6px;
    }
  }
  #content {
    article.esclusivo {
      header {
        min-height: 85px;
        .label {
          top: @baseDim * 2;
        }
      }
    }
    .well {
      article.esclusivo {
        header {
          min-height: 65px;
          .label {
            top: @baseDim + 3;
          }
        }
      }
    }
    .important {
      article.esclusivo {
        header {
          .label {
            top: @baseDim * 2;
          }
        }
      }
    }
  }
}

/* Responsive: Landscape phone to desktop/tablet
-------------------------------------------------- */
@media (max-width: 767px) {
  [class*="span"].pull-right,
  .row-fluid [class*="span"].pull-right {
    float: none;
  }
  body {
    padding-left: 0;
    padding-right: 0;
  }
  #content {
    #sidebar {
      margin-top: @baseDim * 2;
      border-top: @baseDim / 2 solid @border;
      &:before {
        display: none;
      }
      .thumbnail > img {
        margin: 0;
      }
      section {
        &.categorie {
          text-align: left;
        }
        &.newsletter, &.side-block {
          .clearfix {
            p {
              margin-left: @baseDim * 4;
              &:before {
                display: none;
                clear: none;
              }
            }
          }
        }
      }
    }
    .row-sep {
      article {
        &.sep2:before, &.sep3:before {
          display: none;
        }
      }
    }
    .esterni_foglia, #esperto {
      padding-left: @baseDim * 1.5;
      padding-right: @baseDim * 1.5;
    }
    article#foglia {
      padding-left: @baseDim * 1.5;
      padding-right: @baseDim * 1.5;
      aside {
        .well {
          overflow: hidden;
          time.date {
            float: left;
            width: 140px;
          }
          .opzioni {
            float: right;
            width: 140px;
          }
        }
      }
      &.scadenza {
        aside {
          overflow: hidden;
          .day {
            float: left;
            width: 140px;
            time {
              padding: @baseDim / 2;
            }
          }
          .well {
            float: right;
            width: 140px;
          }
        }
      }
      footer {
        section {
          &#correlati {
            article {
              border-bottom: 1px solid @border;
              margin-bottom: @baseDim;
              &:last-child {
                border: none;
                margin-bottom: 0;
              }
            }
          }
          &.argomenti {
            ul {
              padding: 0;
              li {
                display: block;
                margin-bottom: @baseDim / 2;
              }
            }
          }
        }
      }
    }
    #esperto {
      .well-esperto {
        .span6.offset3 {
          margin-left: 30%;
          width: 65%;
        }
        span.icon-quote {
          font-size: @font-size * 3;
          top: @baseDim * 3;
        }
        h3 {
          font-size: @font-size + 8;
        }
      }
    }    
    article {
      .allegati {
        &.multimedia {
          ol {
            li {
              height: 121px;
              margin-bottom: @baseDim / 2;
              a {
                font-size: 16px;
                line-height: 1.2;

                &.thumbnail {
                  margin-right: @baseDim / 2;
                  float: left;
                }
              }
            }
          }
        }
      }
    }
    #abbonati {
      .alert-abbonati {
        padding: 0;
      }
      .accedi {
        form {
          ul {
            li.last {
              display: block;
              margin-top: @baseDim / 2;
            }
          }
        }
      }
    }
    .side_bordered {
      margin-bottom: @baseDim;
      ul {
        overflow: hidden;
        li {
          display: inline-block;
          margin-right: @baseDim;
        }
      }
    }
    #infinite-list {
      .row {
        margin-left: 0;
        .span2 {
          float: left;
          width: 22%;
        }
        .span6 {
          float: left;
          margin-left: 4%;
          width: 70%;
        }
        .span3 {
          float: left;
          width: 32%;
        }
        .span5 {
          float: left;
          margin-left: 4%;
          width: 60%;
        }
      }
    }
    #calendar {
      .day {
        margin-bottom: @baseDim;
        width: 140px;
      }
    }
    header.risultati {
      #ordina {
        text-align: left;
      }
    }
  }
  
  #popup-blocco {
    width: 100%;
    margin:0;
    position:absolute;
    top:0;
    left:0;
  }
  
  #popup-date {
    height: auto;
    width: 100%;
    margin:0;
    position:absolute;
    top:0;
    left:0;
    menu {
      li {
        float: none;
        margin-bottom: @baseDim;
        &.last {
          position: relative;
          bottom: auto;
          right: auto;
        }
      }
    }
  }
}

/* Responsive: Up to landscape phone
-------------------------------------------------- */
@media (max-width: 480px) {
  h1 {
    font-size: @font-size + 10;
  }
  h2 {
    font-size: @font-size + 2;
  }
  
  header#header {
    ul.second_header {
      margin-left: -@baseDim;
      margin-right: -@baseDim;
      position: relative;
      padding-bottom: @baseDim * 2;
      time {
        position: absolute;
        width: 100%;
        text-align: center;
        bottom: 0;
        left: 0;
      }
      li {
        border: none;
        &:first-child {
          padding: 0;
        }
        &.last {
          border-left: 1px solid @border;
        }
      }
    }
  }
  
  #content {
    #infinite-list {
      h5 {
        display: none;
      }
      a.thumbnail {
        display: none;
      }
      figure {
        a.thumbnail {
          display: block;
        }
      }
      .row {
        .span2, .span6, .span3, .span5 {
          float: none;
          width: 100%;
          margin: 0;
        }
      }
      .scadenza {
        .row {
          .day {
            width: 22%;
            float: left;
            margin: 0;
            h6 {
              font-size: @font-size - 10;
              a {
                // display: none;
              }
            }
            time.date {
              padding: @baseDim / 2 0;
              .g {
                font-size: @font-size + 12;
              }
              .ma {
                font-size: @font-size - 6;
              }
            }
          }
          .span6 {
            float: left;
            width: 70%;
            margin-left: 4%;
          }
        }
      }
    }
    #sidebar {
      padding-top: @baseDim;
      section {
        margin-left: 0;
        margin-right: 0;
      }
    }
    .esterni_foglia, #esperto {
      padding-left: 0;
      padding-right: 0;
    }
    .side_bordered {
      ul {
        li {
          display: block;
        }
      }
    }
    #abbonati {
      .accedi {
        form {
          ul {
            li {
              display: block;
              margin-top: @baseDim / 2;
            }
          }
        }
      }
    }
    article#foglia {
      padding: @baseDim 0 0;
      h2 {
        margin-bottom: @baseDim;
        &.title_page {
          font-size: @font-size + 4;
          margin-bottom: @baseDim;
        }
      }
      .contenuto {
        p, ul li, ol li {
          font-size: @font-size - 2;
        }
        img {
          display: block;
          &.float {
            width: 100%;
            height: auto;
          }
          &.float-left, &.float-right {
            float: none;
            margin: @baseDim 0;
          }
        }
      }
      aside {
        margin: 0 -@baseDim;
        .well {
          time.date {
            float: none;
            width: 100%;
            margin-bottom: @baseDim;
            margin-left: @baseDim / 2;
            span {
              &.g, &.ma {
                float: none;
                display: inline;
                width: auto;
                font-size: @font-size + 4;
                margin: 0 3px;
              }
            }
          }
          .opzioni {
            float: none;
            width: auto;
            font-size: @font-size - 4;
            li {
              display: inline-block;
              text-align: center;
              margin: 0;
              padding: 0 @baseDim / 2;
              border-left: 2px solid @border;
              &:first-child {
                border: none;
              }
              a {
                display: block;
                text-align: center;
                span {
                  &.label {
                    display: none;
                  }
                  &.icon {
                    font-size: @font-size + 4;
                    width: @baseDim * 1.5;
                    margin: 0;
                    padding: 0;
                    border: none;
                
                  }
                }
              }
            }
          }
        }
      }
      &.scadenza {
        aside {
          .day {
            float: none;
            width: auto;
            text-align: center;
            margin-left: @baseDim;
            margin-right: @baseDim;
            span {
              &.g, &.ma {
                float: none;
                display: inline;
                width: auto;
                font-size: @font-size + 4;
                margin: 0 3px;
              }
            }
          }
          .well {
            float: none;
            width: auto;
          }
        }
      }
    }
    #esperto {
      .well-esperto {
        .span6.offset3 {
          margin-left: 30%;
          width: 65%;
        }
        span.icon-quote {
          font-size: @font-size * 2;
        }
        h3 {
          font-size: @font-size;
        }
      }
    }
  }
  #sidenav {
    display: none;
  }
  
